.toastify {
  padding: 12px 20px;
  color: #ffffff;
  display: inline-block;
  box-shadow: 0 3px 6px -1px rgba(0, 0, 0, 0.12), 0 10px 36px -4px rgba(77, 96, 232, 0.3);
  background: -webkit-linear-gradient(315deg, #73a5ff, #5477f5);
  background: linear-gradient(135deg, #73a5ff, #5477f5);
  position: fixed;
  opacity: 0;
  transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  border-radius: 2px;
  cursor: pointer;
  text-decoration: none;
  max-width: calc(50% - 20px);
  z-index: 2147483647;
}

.toastify.on {
  opacity: 1;
}

.toast-close {
  opacity: 0.4;
  padding: 0 5px;
}

.toastify-right {
  right: 15px;
}

.toastify-left {
  left: 15px;
}

.toastify-top {
  top: -150px;
}

.toastify-bottom {
  bottom: -150px;
}

.toastify-rounded {
  border-radius: 25px;
}

.toastify-avatar {
  width: 1.5em;
  height: 1.5em;
  margin: -7px 5px;
  border-radius: 2px;
}

.toastify-center_error {
  margin-top: 54px;
  margin-left: auto;
  margin-right: auto;
  max-height: fit-content;
  left: 0;
  right: 0;
  max-width: fit-content;
  max-width: -moz-fit-content;
  background: linear-gradient(to right, #ff585b, #ff0b01);
}
.toastify-center_successful {
  margin-top: 54px;
  margin-left: auto;
  margin-right: auto;
  max-height: fit-content;
  left: 0;
  right: 0;
  max-width: fit-content;
  max-width: -moz-fit-content;
  background: linear-gradient(to right, #00b09b, #96c93d);
}
